.header-v3 {
  // +gradient-y(rgba(0,0,0,.75),rgba(0,0,0,0))
  background-color: $primary;
  // background-image: url(../img/header_bj.jpg)
  background-position: left top;
  background-repeat: repeat;
  position: relative;
  width: 100%;
  // top: 0
  // left: 0
  z-index: 999;
  text-align: center;
  transition: background .35s;
  // border-top: 2px solid $secondary
  &.header-active {
    background: #FFF;
    box-shadow: 0 1px 5px 0 rgba(0,0,0,.35); }
  @include media-breakpoint-down(lg) {
    text-align: right;
 }    // padding-right: 60px
  @include media-breakpoint-down(md) {
    padding-right: 0; }
  @include media-breakpoint-up(md) {
    .navbar-main.collapse {
      display: block !important; } }
  @include media-breakpoint-down(md) {
    @include clearfix; }
  .navbar {
    display: inline-block; }
  .header-logo,.header-hotline {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    @include media-breakpoint-down(md) {
      position: static;
      transform: none;
      display: inline-block; } }
  .header-logo {
    left: 0;
    padding: 15px;
    @include media-breakpoint-up(xl) {
      padding-left: 35px; }
    // position: static
    // float: left
    @include media-breakpoint-down(md) {
      float: left;
      padding: 12px 15px;
      // padding-left: 0
      max-width: 50%; }
    img {
      // max-height: 46px
      width: auto;
      // +media-breakpoint-down(md)
 } }      //   max-height: 36px
  .navbar-main {
    width: 100%;
    // background-color: $primary
    // border-bottom: 3px solid $primary
    // +media-breakpoint-up(md)
 }    //   float: right
  .header-hotline {
    right: 0;
    padding: 20px 15px;
    font-family: 'Impact';
    @include media-breakpoint-up(xl) {
      padding-right: 35px; }
    span {
      color: $secondary;
      font-size: 32px;
      display: inline-block;
      line-height: 1;
      transition: color .35s;
      vertical-align: middle;
      @include media-breakpoint-down(lg) {
        display: none; } }
    @include media-breakpoint-down(md) {
      padding: 15px; }
    @include media-breakpoint-down(md) {
      position: static;
      display: inline-block;
      vertical-align: middle;
      padding: 10px;
      span {
        display: none; } } }
  .hotline-icon {
    color: $secondary;
    font-size: 18px;
    border: 1px solid;
    border-radius: 50%;
    @include size(2em);
    line-height: 2em;
    margin-right: 5px;
    transition: color .35s;
    vertical-align: middle;
    display: inline-block;
    text-align: center;
    // transform: rotate(90deg)
    @include media-breakpoint-down(lg) {
      margin-right: 0; }
    @include media-breakpoint-down(md) {
      text-align: center; }
    @include media-breakpoint-down(md) {
      font-size: 20px;
      margin-right: 0; } }
  .navbar-toggle {
    display: none;
    @include size(40px);
    border: 1px solid;
    border-radius: 50%;
    vertical-align: middle;
    margin-right: 15px;
    color: $secondary;
    transition: color .35s;
    @include media-breakpoint-down(md) {
      display: inline-block;
      margin-right: 0; } } }
