 } } } } }          //   background-color: #eee
.header-active {
  .header-hotline {
    color: $primary; }
  .navbar-toggle {
    color: $primary; } }
@include media-breakpoint-down(md) {
  .header-v3 {
    .navbar-main {
      position: fixed;
      height: 100% !important;
      width: 60%;
      overflow-x: hidden;
      top: 0;
      left: 0;
      z-index: 1199;
      overflow-y: auto;
      background-color: $primary;
      transform: translate3d(-100%,0,0);
      transition: transform .35s;
      &.navbar-open {
        transform: translate3d(0,0,0);
        box-shadow: 0 0 10px rgba(0,0,0,0.5); }
      > .container {
        padding: 0; } }

    .dropdown-box {
      max-height: 0;
      display: block;
      overflow: hidden;
      opacity: 0;
      transition: opacity .35s;
      padding-left: 30px !important;
      background-color: darken($primary,8%);
      > li {
        > a {
          border-top: 1px solid darken($primary,8%);
          border-left: 1px solid darken($primary,8%);
          color: #ffffff;
          &:hover {
            background-color: darken($primary,15%); } } } }
    .dropdown-btn {
      display: block;
      float: right;
      text-align: center;
      border-left: 1px solid darken($primary,8%);
      &:before {
        content: "+"; }
      &.active {
        &:before {
          content: "-"; }
        ~ .dropdown-box {
          opacity: 1;
          max-height: none; } } }
    .navbar-open {
      .nav-header-v3 {
        > li {
          opacity: 1;
          transform: translate3d(0,0,0); } } }
    @for $i from 3 through 12 {
      .nav-header-v3 {
        > li:nth-child(#{$i - 2}) {
          transition-delay: $i * .1s !important; } } } }
  .navbar-backdrop-close {
    @include size(100%);
    position: fixed;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity .25s;
    background-color: #000000;
    z-index: 997;

    &.open {
      opacity: .6; } }
  .header-v3 {
    .navbar {
      display: block; } }
  .nav-header-v3 {
    display: block !important;
    text-align: left;
    a {
      color: #FFF;
      display: block;
      padding: 10px 15px;
      background-color: $primary; }
    > li {
      transition: opacity .35s, transform .35s;
      opacity: 0;
      transform: translate3d(0,100%,0);
      border-bottom: 1px solid darken($primary,8%);
      > a {
        color: #FFF;
        &:hover {
          background-color: darken($primary,8%); } }
      &.dropdown {
        > a {
          width: calc( 100% - 40px );
          &.dropdown-btn {
            width: 40px; } } } } } }
