
@mixin font-format($font-size, $line-height, $fixlh: false) {
  font-size: $font-size;
  @if $fixlh {
    line-height: $line-height; }
  @else {
    line-height: ($line-height/$font-size); } }
@mixin centering-elements($horizontal: true, $vertical: true) {
  position: absolute;
  @content;
  @if $horizontal and $vertical {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  @else if $horizontal {
    left: 50%;
    transform: translate(-50%, 0); }
  @else if $vertical {
    top: 50%;
    transform: translate(0, -50%); } }

// Header
.header-group {
  position: relative; }
.header-top {
  padding: 10px 0;
  background-color: $black;
  .box-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .sub_office a,.top-login a {
      display: inline-block;
      color: $white;
      transition: color 0.35s linear;
      &:hover {
        color: $secondary; }
      i {
        margin-right: 10px;
        font-size: 20px; } }
    .icon-top-shopping {
      margin-left: 30px; } }
  .login-grp {
    @include clearfix;
    // +row-gutter(15px, 'div')
    padding-right: 40px;
    float: right;
    @include media-breakpoint-down(lg) {
      padding-right: 30px; }
    @include media-breakpoint-down(md) {
      padding-right: 20px; }
    @include media-breakpoint-down(sm) {
      float: none;
      padding-right: 0;
      margin-bottom: 10px; }
    &-item {
      display: block;
      float: left;
      &:not(:last-of-type) {
        margin-right: 15px; } }
    img, span {
      display: inline-block;
      vertical-align: middle; }
    img {
      @include size(20px);
      margin-right: 10px;
      @include media-breakpoint-down(sm) {
        margin-right: 5px; } }
    span {
      @include font-format(14px, 20px, true);
      color: #fff; } }
  .link-grp {
    @include clearfix;
    // +row-gutter(12px, 'a')
    padding-right: 40px;
    float: right;
    @include media-breakpoint-down(lg) {
      padding-right: 30px; }
    @include media-breakpoint-down(md) {
      padding-right: 20px; }
    @include media-breakpoint-down(sm) {
      float: none;
      padding-right: 0; }
    a {
      @include font-format(14px, 20px, true);
      display: block;
      float: left;
      color: #ff0000;
      transition: .35s;
      &:not(:last-child) {
        margin-right: 12px; }
      &:hover {
        color: $white; } } } }
.header-search,.mobile-search {
  width: 320px;
  @include media-breakpoint-down(lg) {
    width: 250px; }
  .form-group {
    margin-bottom: 0;
    border-bottom: 1px solid $primary;
    align-items: center;
    .form-control {
      border: 0;
      padding-left: 0;
      font-size: 14px;
      &:placeholder {
        color: #999999;
        font-size: 14px; } }
    button {
      background: none;
      border: 0;
      padding: 0;
      color: $primary;
      transition: color 0.35s linear;
      i {
        font-size: 22px; }
      &:hover {
        color: $secondary; } } } }

.header-wrap {
  @include media-breakpoint-down(md) {
    padding: 10px 0; }
  @include media-breakpoint-down(sm) {
    padding: 10px;
    .navbar-toggle {
      color: #333;
      vertical-align: middle; } }
  .logo-index {
    display: none; }
  &.header-index:not(.header-active) {
    background-color: rgba($black, .3);
    .logo-all {
      display: none; }
    .logo-index {
      display: block; }
    .nav-header-custom > li {
      &.active, &:hover {
        > a {
          border-bottom-color: $white; } }
      > a {
        color: $white; } } }
  &.header-active {
    position: fixed;
    top: 0 !important;
    bottom: auto;
    background-color: $white; }
  .header-left, .header-right {
    display: table-cell;
    vertical-align: middle; }
  .header-right {
    text-align: right; }
  h1 {
    line-height: 1;
    margin: 0 -30px;
    @include media-breakpoint-down(xl) {
      margin: 0 -20px; }
    @include media-breakpoint-down(lg) {
      margin: 0 -15px; } }
  .logo {
    display: inline-block;
    vertical-align: bottom;
    @include media-breakpoint-down(xl) {
      img {
        max-height: 60px;
        transition: .35s; } }
    @include media-breakpoint-down(md) {
      img {
        max-height: 40px; } } }
  .navbar-main {
    display: inline-block; } }
.header-menu-box {
  border-bottom: 1px solid #cccccc;
  .header-logo {
    padding: 20px 0;
    @include media-breakpoint-down(md) {
      padding: 0; } } }
.header-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center; }
.header-menu {
  flex: 1;
  @include media-breakpoint-down(md) {
    display: none; } }
@include media-breakpoint-up(lg) {
  .nav-header-custom {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    > li {
      padding: 0 30px;
      // +media-breakpoint-down(mxl)
      //   padding: 0 20px
      // +media-breakpoint-down(lg)
      //   padding: 0 15px
      &.active, &:hover {
        > a {
          // border-bottom-color: $secondary
          color: $secondary; } }
      > a {
        font-size: 18px;
        display: block;
        padding: 34px 0;
        color: #333333;
        text-align: center;
        border-bottom: 3px solid transparent;
        position: relative;
        transition: .35s;
        // +media-breakpoint-down(xl)
 } }        //   font-size: 16px
    i {
      font-size: 14px;
      line-height: 1;
      position: absolute;
      top: 50%;
      right: -20px;
      transform: translateY(-50%);
      @include media-breakpoint-down(mxl) {
        font-size: 12px;
        right: -15px; } }
    .drop-level-0 {
      position: relative;
      &:hover {
        .drop-menu-level-0 {
          opacity: 1;
          visibility: visible; } } } }
  .drop-menu-level-0, .drop-menu-level-1 {
    padding: 0px;
    min-width: 100%;
    position: absolute;
    background-color: $white;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    transition: opacity .35s ease;
    > li {
      padding: 0 35px;
      text-align: left;
      position: relative;
      border-bottom: 1px solid transparent;
      transition: border-color 0.35s linear;
      &:hover {
        border-color: $secondary; }
      // +media-breakpoint-down(xl)
      //   padding: 0 20px
      // +media-breakpoint-down(lg)
      //   padding: 0 15px
      > a {
        @include font-format(16px, 28px);
        white-space: nowrap;
        font-weight: 100;
        color: #282828;
        transition: .35s;
        line-height: 3;
        // +media-breakpoint-down(lg)
        //   padding: 0 15px
        //   font-size: 14px
        &:hover {
          color: $secondary; } } } }
  .drop-menu-level-0 {
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    > li:hover .drop-menu-level-1 {
      opacity: 1;
      visibility: visible; } }
  .drop-menu-level-1 {
    top: 0px;
    left: 100%;
    .drop-menu {
      display: block;
      min-width: 120%;
      padding: 10px 30px;
      border-top: 3px solid $secondary;
      position: absolute;
      top: 0;
      left: 90%;
      background-color: $white;
      box-shadow: 0 0 6px rgba(71, 70, 70, 0.15);
      opacity: 0;
      visibility: hidden;
      transition: .35s;
      z-index: 999;
      &.left-side {
        left: auto;
        right: 100%; }
      // +media-breakpoint-down(xl)
      //   padding: 10px 25px
      // +media-breakpoint-down(lg)
      //   padding: 10px 20px
      a {
        @include font-format(14px, 28px);
        color: #555555;
        z-index: 999;
        transition: .35s;
        // +media-breakpoint-down(lg)
        //   font-size: 12px
        &::before {
          @include size(3px);
          content: "";
          display: inline-block;
          vertical-align: middle;
          border-radius: 50%;
          margin-right: 8px;
          background-color: #555; }
        &:hover {
          color: $secondary; } } } } }

/*Mobile Header*/
.modal-mobile-nav {
  width: 100%;
  max-width: 240px;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: auto;
  @include media-breakpoint-down(sm) {
    max-width: 200px; }
  .modal-content {
    background: transparent;
    min-height: 100%;
    border: 0;
    border-radius: 0; }
  &.fade {
    .modal-dialog {
      width: 100%;
      background: $white;
      margin: 0;
      min-height: 100%;
      transform: translate(100%,0); } }
  &.show {
    .modal-dialog {
      transform: translate(0,0); }
    .nav {
      > li {
        transform: translateX(0);
        opacity: 1;
        visibility: visible; } } }
  .mobile-search {
    max-width: 90%;
    margin: 10px auto;
    .search-form {
      .form-control {
        padding: 7px 15px {
          right: 35px; }
        font-size: 12px; } } }
  .mobile-logo {
    a {
      padding: 5px;
      margin-bottom: 10px; } }
  .nav {
    background-color: $white;
    li {
      transform: translateX(50px);
      opacity: 0;
      visibility: hidden;
      transition: all 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
      @include clearfix;
      @for $i from 1 through 10 {
        &:nth-child(#{$i}) {
          transition-delay: .15s * $i; } }
      a {
        float: left;
        width: 100%;
        margin-bottom: 1px;
        padding: 0 15px;
        font-size: 14px;
        line-height: 40px;
        text-align: left;
        color: #666;
        transition: all .35s;
        position: relative;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        i {
          display: none; } } }
    .drop {
      &.active {
        > a {
          color: $primary; } }
      > a {
        width: calc( 100% - 40px); }
      .btn-drop {
        float: right;
        @include size(40px);
        padding: 0;
        &.active {
          color: $primary;
          &:after {
            transform: translate(-50%, -50%) rotate(-135deg); }
          ~ a {
            color: $primary; } }
        &:after {
          content: '';
          @include size(6px);
          border-bottom: 1px solid;
          border-right: 1px solid;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
          transition: all .35s; } } }
    .drop-menu {
      @include size(100%, 0);
      margin-top: 1px;
      overflow: hidden;
      transition: all .35s;
      position: relative;
      &.active {
        > li {
          transform: translateX(0);
          opacity: 1;
          visibility: visible; } }
      > li {
        background-color: $white; }
      @for $i from 0 through 5 {
        &.drop-menu-level-#{$i} li {
          a:not(.btn-drop) {
            padding-left: 30px + ($i * 10px); } } } } } }
/*End Header*/
