[data-toggle="img"] {
  position: relative;
  img {
    opacity: 0;
    display: block;
    @include size(0px); }
  &:after {
    position: absolute;
    @include size(100%);
    top: 0;
    left: 0;
    content: "";
    display: block;
    opacity: 1;
    transition: opacity .35s;
    z-index: 2;
    background-color: #eee; } }
.img-cover,.img-contain {
  background-position: center center;
  background-repeat: no-repeat;
  // background-color: #eee
  position: relative; }
.img-cover {
  background-size: cover; }
.img-contain {
  background-size: contain; }
.img-loaded {
  &:after {
    opacity: 0; } }
.img-parallax {
  overflow: hidden;
  position: relative; }
.parallax-bg {
  position: absolute;
  top: 0;
  min-height: 100%;
  width: 100%;
  left: 0;
  z-index: 1; }
.z-index-3 {
  position: relative;
  z-index: 3; }
