.banner {
  background-repeat: no-repeat;
  // position: relative
  background-color: #eee;
  overflow: hidden;
  display: block;
  background-size: cover;
  &:before {
    display: block;
    content: "";
    position: relative;
    z-index: 1;
 } }    // background-color: #000 !important
.banner-index {
  &:before {
    padding-top: 600px;
    @include media-breakpoint-down(lg) {
      padding-top: percentage(600/1920); } } }
.banner-page {
  &:before {
    padding-top: percentage(600/1400); } }
