@mixin flex($align: flex-start, $justify: flex-start, $wrap: nowrap) {
		display: flex;
		align-items: $align;
		justify-content: $justify;
		flex-wrap: $wrap; }

@mixin font-format($font-size, $line-height, $fixlh: false) {
		font-size: $font-size;
		@if $fixlh {
				line-height: $line-height; }
		@else {
				line-height: ($line-height/$font-size); } }

@mixin centerElement($pos) {
		position: absolute;
		@content;
		@if $pos == 'both' {
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%); }
		@else if $pos == "left" {
				left: 50%;
				transform: translateX(-50%); }
		@else if $pos == "top" {
				top: 50%;
				transform: translateY(-50%); } }
@media screen and (min-width:1024px) and (max-width: 1460px) {
	@-moz-document url-prefix() {
		html {
			transform-origin: 0 0;
			overflow-x: hidden; } } }

@media screen and (max-width: 1400px) and (min-width: 1251px) {
	html {
		zoom: 0.85; }
	@-moz-document url-prefix() {
		html {
				transform: scale(0.85);
				width: calc(100%/0.85); } } }

@media screen and (max-width: 1110px) and (min-width: 1024px) {
	html {
		zoom: 0.7; }
	@-moz-document url-prefix() {
		html {
			transform: scale(0.7);
			width: calc(100%/0.7); } } }
@media screen and (max-width: 767.98px) {
	html {
		zoom: 0.8; }
	@-moz-document url-prefix() {
		html {
			transform: scale(0.8);
			width: calc(100%/0.8); } } }

.stretched-link::after {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1;
		pointer-events: auto;
		content: "";
		background-color: rgba(0,0,0,0); }

.img-parallax {
		overflow: hidden;
		position: relative;
		background-attachment: fixed; }

.zoom {
		.img {
				overflow: hidden; }
		.ratio,img {
				transition: transform .35s; }
		&:hover {
				.ratio, img {
						transform: scale(1.1); } } }

.modal-dialog-video {
		max-width: 800px;
		overflow: hidden;
		border-radius: 10px;
		iframe {
				display: block; }
		.modal-content {
				position: relative;
				button {
						position: absolute;
						right: 10px;
						top: 5px;
						span {
								color: #ffffff; } } } }

.container-custom {
		@include media-breakpoint-up(xxl) {
			max-width: 1400px; }
		@include media-breakpoint-between(lg, mxl) {
			max-width: 1440px; } }
body {
		position: relative;
		min-height: 100vh; }

p,h1,h2,h3,h4,h5,h6,span {
		margin: 0; }

.btn-primary:not(:disabled):not(.disabled):active {
	color: $secondary;
	background-color: transparent;
