/*------------------------------------
 *  Box-shadows-v2
 *------------------------------------ */
@mixin shadow-v2($size: 10px, $glow: $size, $inset: $size / 1.5 ,$darken: 0.2) {
  box-shadow: 0 $size $glow (-$inset) rgba(0,0,0,$darken); }
.shadow-v2 {
  @include shadow-v2(12px); }
@for $i from 1 through 4 {
  .shadow-v2-#{$i},.shadow--hover-v2-#{$i}:hover {
    @include shadow-v2(6px * $i); } }
