@include media-breakpoint-down(sm) {
  .nav.nav-color {
    > li {
      border: 0 !important; }
    @for $i from 1 through 16 {
      > li:nth-child(#{$i}) {
        > a {
          background-color: mix(#937bdb, $primary, ($i - 1) * 6.25%);
          &:hover {
            background-color: darken($primary,10%); } } } } } }

