.label-required {
  position: relative;
  &:before {
    content: "*";
    color: $danger;
    position: absolute;
    display: inline-block;
    vertical-align: middle;
    width: 0.75em;
    margin-left: -0.75em; } }
.help-block {
  display: none; }
.has-error {
  .form-control {
    border-color: $danger; }
  .help-block {
    margin-top: 5px;
    display: block;
    color: $danger; }
  label {
    color: $danger !important; }
  .checker .checker-radio:before, .checker .checker-checkbox:before {
    border-color: $danger; } }
