#footer {
  background-color: #fafafa;
  padding-top: 50px;
  // padding-bottom: 50px
  // padding: 15px 0 0
  // border-top: 3px solid $primary
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  @include media-breakpoint-down(sm) {
    padding-top: 25px; } }
.ft-form-tit {
  font-size: 24px;
  color: $primary;
  font-weight: bold;
  margin-bottom: 30px;
  span {
    font-size: 14px;
    font-weight: normal; } }
.ft-form {
  .row {
    @include row-gutter(8px, div); }
  @include media-breakpoint-down(sm) {
    margin-bottom: 20px; }
  .form-control {
    border-radius: 6px;
    &[name='captcha'] {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0; } }
  .captcha-img {
    border: 1px solid #ced4da;
    border-left: 0;
    border-top-right-radius: 6px;
    border-bottom-right-radius: 6px;
    overflow: hidden; }
  .btn-form {
    background-color: $secondary;
    border-radius: 6px;
    border-color: $secondary;
    &:hover {
      background-color: darken($secondary, 10%);
      border-color: darken($secondary, 10%); } }
  .has-error .captcha-img {
    border-color: #dc3545; } }
.ft-menu-block {
  width: 50%;
  margin: 0 auto;
  @include media-breakpoint-down(sm) {
    width: 100%;
    margin-bottom: 30px; }
  .ft-menu-tit {
    font-size: 18px;
    color: $primary;
    position: relative;
    padding-bottom: 20px;
    margin-bottom: 20px;
    font-weight: bold;
    &:before {
      position: absolute;
      content: '';
      left: 0;
      bottom: 0;
      height: 2px;
      width: 2em;
      background-color: $secondary; } }
  .ft-menu-list {
    @include clearfix;
    a {
      width: 50%;
      padding: 7px 5px 7px 0;
      color: $primary;
      float: left;
      @include media-breakpoint-down(sm) {
        width: 33.333%; }
      &:hover {
        color: darken($primary, 15%); } } } }
.ft-qr-block {
  @include media-breakpoint-only(md) {
    padding-top: 30px; }
  img {
    width: 120px;
    float: left;
    margin-right: 20px;
    @include media-breakpoint-down(lg) {
      width: 85px; } }
  .txt {
    overflow: hidden;
    p {
      font-size: 14px;
      margin-bottom: 7px;
      color: $primary;
      @include media-breakpoint-down(lg) {
        font-size: 12px;
        margin-bottom: 0; } }
    span {
      color: $primary;
      font-size: 30px;
      @include media-breakpoint-down(lg) {
        font-size: 20px; } } } }
.friend-link {
  margin-top: 50px;
  padding-top: 20px;
  padding-bottom: 20px;
  border-top: 1px solid rgba($primary, .2);
  @include clearfix;
  @include media-breakpoint-down(sm) {
    margin-top: 25px; }
  .friend-name {
    color: $primary;
    display: inline-block;
    float: left;
    margin-bottom: 5px;
    @include media-breakpoint-down(sm) {
      float: none; } }
  .friend-list {
    display: inline-block;
    // float: left
    overflow: hidden;
    width: calc(100% - 5em);
    @include clearfix;
    @include media-breakpoint-down(sm) {
      width: 100%; }
    li {
      margin-bottom: 5px;
      display: inline-block;
      // padding-right: 10px
      float: left;
      a {
        padding-right: 10px;
        margin-right: 10px;
        color: $primary;
        @include media-breakpoint-down(sm) {
          padding-right: 5px;
          margin-right: 5px; } }
      &:not(:last-child) {
        a {
          border-right: 1px solid $primary; } } } } }
.copyright {
  background-color: transparent;
  text-align: center;
  color: #666666;
  margin-top: 60px;
  font-size: 14px;
  border-top: 1px solid transparent;
  @include media-breakpoint-down(md) {
    margin-top: 30px; }
  // +media-breakpoint-only(md)
  //   padding-bottom: 0px
  @include media-breakpoint-down(sm) {
    padding-top: 10px;
    padding-bottom: 92px; }
  span,a {
    display: inline-block;
    padding: 20px 7px;
    color: #888888;
    transition: .35s;
    @include media-breakpoint-down(sm) {
      padding: 10px 7px; } }
  span a {
    padding-top: 0;
    padding-bottom: 0; }
  a:hover {
    color: $secondary; }
  .beian {
    img {
      width: 20px;
      height: 20px;
      max-width: 20px;
      margin-right: 5px; } } }

