#right-tool {
  position: fixed;
  bottom: 30%;
  right: 10px;
  // transform: translateY(-50%)
  z-index: 100;
  // box-shadow: 0 0 5px rgba(0, 0, 0, 0.43)
  @include media-breakpoint-down(sm) {
    display: none; }
  li {
    position: relative;

    &:not(:last-child) {
      margin-bottom: 6px; }
    &.gotop {
      > a {
        background-color: $primary; } }
    > a {
      border-radius: 50%;
      @include size(40px);
      line-height: 40px;
      text-align: center;
      background-color: $primary;
      transition: all .35s;
      overflow: hidden;
      i {
        font-size: 24px;
        color: #fff; } }
    .hover-show {
      position: absolute;
      top: 0;
      right: 100%;
      background-color: $primary;
      padding: 15px;
      transform: translateY(20px);
      opacity: 0;
      visibility: hidden;
      white-space: nowrap;
      transition: all .35s;
      .txt {
        font-size: 12px;
        color: #fff;
        margin-bottom: 0; }
      .qrcode {
        width: 100px;
        max-width: none;
        margin-bottom: 5px; }
      a {
        font-size: 20px;
        color: #fff;
        white-space: nowrap;
        h5 {
          font-size: 14px;
          margin-bottom: 5px; }
        span {
          font-size: 22px;
          display: block;
          line-height: 1.3; } } }
    &.write-form, &.gotop {
      a {
        height: 0; }
      &.active {
        a {
          height: 40px; } } }
    &:hover {
      > a {
        background-color: $secondary;
        i {
          color: #fff; } }
      .hover-show {
        transform: translateY(0);
        opacity: 1;
        visibility: visible; } } } }
