/*------------------------------------
 *  Box-shadows-v3-v4-v5
 *------------------------------------ */

@mixin shadow-v3($size: 10px,$pos: 0,$darken: 0.2) {
  position: relative;
  &:after,&:before {
    content: "";
    display: none;
    position: absolute;
    top: 80%;
    max-width: 300px;
    width: 50%;
    background: rgba(0,0,0,$darken);
    z-index: -1;
    bottom: ($size * 3);
    box-shadow: 0 ($size * 3) ($size * 2) rgba(0,0,0, 0.2); }
  @if $pos != 1 {
    &:after {
      display: block;
      right: $size;
      transform: rotate(3deg); } }
  @if $pos != 2 {
    &:before {
      display: block;
      left: $size;
      transform: rotate(-3deg); } }
  &:hover {
    &:after,&:before {
      box-shadow: 0 ($size * 3) ($size * 2) rgba(0,0,0, 0.2) !important; } } }
[class*="shadow--hover-v3"] {
  &:after,&:before {
    box-shadow: none !important;
    transition: box-shadow .35s ease; } }
.shadow-v3 {
  @include shadow-v3(10px); }
.shadow-v3-left {
  @include shadow-v3(10px,1); }
.shadow-v3-right {
  @include shadow-v3(10px,2); }
.shadow--hover-v3-left {
  @include shadow-v3(10px,1); }
.shadow--hover-v3-right {
  @include shadow-v3(10px,1); }
