.checker {
  padding-top: calc(0.375rem + 1px);
  padding-bottom: calc(0.375rem + 1px);
  margin-bottom: 0;
  display: inline-block;
  font-size: inherit;
  line-height: 1.5;
  margin-right: $grid-gutter-width / 2;
  cursor: pointer;
  .checker-radio,.checker-checkbox {
    position: relative;
    padding-left: 1.5em;
    display: block;

    &:before,&:after {
      color: #aaa;
      position: absolute;
      left: 0;
      top: 3px;
      content: "";
      display: block;
      font-size: 16px; }
    &:before {
      @include size(1em);
      border: 1px solid; }
    &:hover {
      &:before {
        color: $primary; } } }
  .checker-radio {
    &:before,&:after {
      border-radius: 50%; }
    &:after {
      opacity: 0;
      border: 0.25em solid;
      top: 7px;
      left: 0.25em; } }
  .checker-checkbox {
    &:before {
      border-radius: 3px; }
    &:after {
      opacity: 0;
      border-radius: 1px;
      border-bottom: 2px solid;
      border-right: 2px solid;
      @include size(0.4em,0.6em);
      top: 5px;
      left: 0.325em;
      transform: rotate(45deg); } }
  input:checked + .checker-radio, input:checked + .checker-checkbox {
    &:after,&:before {
      color: $primary; }
    &:after {
      opacity: 1; } } }
.checker-sm {
  font-size: 0.875rem; }
