/*------------------------------------
 *  Box-shadows-v1
 *------------------------------------ */
@mixin shadow-v1($size: 5px,$darken: 0.1) {
  box-shadow: 0 0 $size rgba(0,0,0,$darken); }
.shadow-v1,.shadow--hover-v1:hover {
  @include shadow-v1(5px); }

@for $i from 1 through 4 {
  .shadow-v1-#{$i},.shadow--hover-v1-#{$i}:hover {
    @include shadow-v1(5px * $i); } }

