.header-v4 {
  padding: 10px 0;
  background-color: #fff;
  &.header-init {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-100%);
    transition: all .35s;
    &.header-active {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      box-shadow: 0 2px 10px rgba(#000, .3); } }
  .header-wrap {
    padding: 0 80px;
    @include media-breakpoint-down(mxl) {
      padding: 0 40px; }
    @include media-breakpoint-down(md) {
      padding: 0 15px; }
    > * {
      vertical-align: middle; } }
  .header-logo {
    width: 15%;
    @include media-breakpoint-down(md) {
      img {
        height: 40px; } }
    @include media-breakpoint-down(md) {
      width: 25%; }
    @include media-breakpoint-down(sm) {
      width: 75%; } }
  .header-action {
    width: 15%;
    text-align: right;
    @include media-breakpoint-down(lg) {
      width: 5%; }
    @include media-breakpoint-down(md) {
      width: 75%; }
    @include media-breakpoint-down(sm) {
      width: 25%; }
    .search-bread {
      vertical-align: middle;
      margin-right: 10px;
      .input-group {
        height: 36px; }
      .form-control {
        border: 1px solid $primary;
        border-left: 3px solid $primary;
        border-radius: 0; }
      .icon {
        font-size: 20px;
        line-height: 1;
        border-radius: 0;
        border: 0; } } }
  .navbar-toggle {
    vertical-align: middle;
    color: #333;
    padding: 2px 0;
    height: auto;
    border: 1px solid #333;
    width: 36px;
    .bar {
      margin: 6px auto;
      width: 22px; } }
  .header-hotline {
    @include media-breakpoint-down(lg) {
      @include size(36px);
      line-height: 34px;
      display: inline-block;
      // margin-left: auto
      text-align: center;
      vertical-align: middle; }
    @include media-breakpoint-down(md) {
      margin-right: 10px;
      line-height: initial;
      border: 1px solid #333; }
    i {
      display: inline-block;
      font-size: 26px;
      vertical-align: middle;
      margin-right: 10px;
      color: #333;
      line-height: 1;
      @include media-breakpoint-down(lg) {
        margin-right: 0; }
      @include media-breakpoint-down(md) {
        font-size: 22px;
        line-height: 34px;
        display: block; } }
    span {
      display: inline-block;
      font-size: 24px;
      line-height: 1;
      font-weight: bold;
      vertical-align: middle;
      color: #333;
      @include media-breakpoint-down(mxl) {
        font-size: 20px; }
      @include media-breakpoint-down(xl) {
        font-size: 18px; }
      @include media-breakpoint-down(lg) {
        display: none; } } }
  .header-menu {
    width: 70%;
    padding: 0 15px;
    @include media-breakpoint-down(lg) {
      width: 80%; } } }
.nav-header-v4 {
  display: flex;
  justify-content: center;
  @include media-breakpoint-down(lg) {
    justify-content: flex-end; }
  li {
    a {
      white-space: nowrap;
      color: #333;
      transition: all .35s;
      i {
        margin-left: 10px;
        font-size: 12px;
        color: #333;
        transition: all .35s; } }
    &:hover {
      > a {
        color: $primary;
        i {
          color: $primary; } } } }
  > li {
    > a {
      padding: 20px;
      font-size: 16px;
      position: relative;
      @include media-breakpoint-down(mxl) {
        padding: 15px; }
      @include media-breakpoint-down(xl) {
        font-size: 14px; }
      &::before {
        position: absolute;
        width: 0;
        height: 3px;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        content: '';
        display: block;
        transition: all .35s; } }
    &:hover, &.active {
      > a {
        color: $primary;
        &::before {
          width: 3em;
          background-color: $primary; } } }
    &.active {
      > a {
        font-weight: bold; } } }
  .drop {
    position: relative;
    .drop-menu {
      position: absolute;
      min-width: 100%;
      z-index: 10;
      background-color: #fff;
      box-shadow: 0 3px 10px rgba(#000, .35);
      opacity: 0;
      visibility: hidden;
      transition: all .35s;
      a {
        padding: 10px 15px;
        text-align: center;
        transition: all .35s; }
      li:hover {
        > a {
          color: #ffffff;
          background-color: $primary;
          i {
            color: #ffffff; } } }
      &.drop-menu-level-0 {
        top: 100%;
        left: 50%;
        transform: translateX(-50%) translateY(10px); }
      &.drop-menu-level-1, &.drop-menu-level-2 {
        top: 0;
        left: 100%;
        transform: translateY(10px); } }
    &:hover {
      > .drop-menu {
        opacity: 1;
        visibility: visible;
        &.drop-menu-level-0 {
          transform: translateX(-50%) translateY(0); }
        &.drop-menu-level-1, &.drop-menu-level-2 {
          transform: translateY(0); } } } } }

// modal nav
.modalNav-v4 {
  // display: block
  .modal-dialog {
    margin: {
      top: 0;
      bottom: 0;
      right: 0;
      left: auto; }
    height: 100%;
    max-width: 300px;
    @include media-breakpoint-down(md) {
      max-width: 250px; } }
  .modal-content {
    border: 0;
    height: 100%;
    border-radius: 0;
    position: relative;
    .close {
      position: absolute;
      top: 0;
      right: 100%;
      padding: 10px;
      background-color: #fff;
      @include size(50px);
      opacity: 1;
      &::before, &::after {
        content: '';
        display: block;
        @include size(22px, 0);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-top: 2px solid;
        // margin: 6px auto
        transition: transform .35s ease, opacity .35s ease; }
      &::before {
        transform: translate(-50%, -50%) rotate(45deg); }
      &::after {
        transform: translate(-50%, -50%) rotate(-45deg); } } }
  &.modal.fade {
    .modal-dialog {
      transform: translateX(100%); }
    &.show {
      .modal-dialog {
        transform: translateX(0); }
      .nav-mobile-header-v4 > li {
        transform: translateX(0);
        opacity: 1;
        visibility: visible; } } } }
.nav-mobile-header-v4 {
  li {
    transform: translateX(50px);
    opacity: 0;
    visibility: hidden;
    transition: all 0.35s;
    a {
      color: #333;
      line-height: 1.8;
      padding: 10px 0;
      padding-left: 20px;
      padding-right: 15px;
      font-size: 14px;
      @include text-truncate;
      i {
        display: none; } }
    &:not(:last-child) {
      border-bottom: 1px solid #eaeaea; }
    &.drop {
      @include clearfix;
      position: relative;
      > a {
        width: calc( 100% - 60px );
        padding-right: 0; }
      .drop-menu {
        height: 0;
        width: 100%;
        overflow: hidden;
        transition: all .35s;
        background-color: rgba(#000, .05);
        li {
          border-top: 1px solid #eaeaea;
          border-bottom: 0;
          a:not(.btn-drop) {
            padding-left: 35px;
            &::before {
              content: "\2022";
              display: inline-block;
              width: 1em;
              margin-left: -1em;
              color: #888; } } } }
      >.btn-drop {
        @include size(60px, 45px);
        position: relative;
        line-height: 45px;
        float: right;
        padding: 0;
        text-align: center;
        &::before {
          content: "\e8b5";
          font-size: 16px;
          font-family: "iconfont"; }
        &.active {
          &::before {
            content: "\e8ca"; }
          ~ .drop-menu {
            > li {
              transform: translateX(0);
              opacity: 1;
              visibility: visible; } } } } } } }
@for $i from 1 through 5 {
  .nav-mobile-header-v4 li.drop .drop-menu-level-#{$i} li {
    a:not(.btn-drop) {
      padding-left: 35px + ($i * 15px); } } }
@for $i from 1 through 10 {
  .nav-mobile-header-v4 {
    li:nth-child(#{$i}) {
      transition-delay: .15s * $i; } } }
