.notify ~ .notify {
  z-index: 1051; }
.modal-backdrop ~ .modal-backdrop {
  z-index: 1050; }

.notify {
  @include media-breakpoint-up(md) {
    padding-right: 17px; }
  .notify-desc {
    color: #999; }
  .modal-dialog {
    max-width: 420px; }
  .modal-content {
    border-radius: 10px; }
  .modal-footer {
    padding: 0;
    @include flex;
    > .btn {
      margin: 0;
      -ms-flex-preferred-size: 0;
      flex-basis: 0;
      border-color: transparent !important;
      -ms-flex-positive: 1;
      padding-top: 10px;
      padding-bottom: 10px;
      flex-grow: 1;
      max-width: 100%;
      border-radius: 0;
      background-color: #FFF;
      color: #444;
      border-left: 1px solid #e9ecef !important;
      &:hover {
        background-color: #f6f6f6; }
      &:active {
        background-color: #e8e8e8; }
      &.btn-ok,&.btn-primary {
        color: $primary; } }
    > .btn:first-child {
      border-left: 0 !important;
      border-bottom-left-radius: 10px; }
    > .btn:last-child {
      border-bottom-right-radius: 10px; } }
  .ie9 & {
    .notify {
      .modal-footer {
        display: table;
        > .btn {
          display: table-cell; } } } } }
.notify-alert {
  .modal-dialog {
    max-width: 360px; } }
.notify-auto {
  color: #999999;
  font-size: 12px;
  text-align: center; }
