/*------------------------------------
 *  Box-shadows-v7
 *------------------------------------ */
@mixin shadow-v4-left($size: 10px, $darken: 0.075) {
  box-shadow: (-$size) $size $size * 2 (-$size) rgba(0,0,0,$darken); }
@mixin shadow-v4-right($size: 10px, $darken: 0.075) {
  box-shadow: $size $size $size * 2 (-$size) rgba(0,0,0,$darken); }
.shadow-v4-left {
  @include shadow-v4-left(10px); }
.shadow-v4-right {
  @include shadow-v4-right(10px); }
@for $i from 1 through 4 {
  .shadow-v4-left-#{$i},.shadow--hover-v4-left-#{$i}:hover {
    @include shadow-v4-left(5px * $i, 0.075 * (1 + ($i - 1) * 0.5)); }
  .shadow-v4-right-#{$i},.shadow--hover-v4-right-#{$i}:hover {
    @include shadow-v4-right(5px * $i, 0.075 * (1 + ($i - 1) * 0.5)); } }
