.navbar-normal {
  .nav {
    @include flex;
    @include media-breakpoint-up(md) {
      > li {
        -ms-flex-preferred-size: 0;
        flex-basis: 0;
        -ms-flex-positive: 1;
        flex-grow: 1;
        max-width: 100%; } } }
  @include media-breakpoint-up(md) {
    .nav-icon {
      display: none; } }
  .ie9 & {
    .nav {
      display: table;
      > li {
        display: table-cell; } } } }
.navbar-auto {
  .nav {
    @include flex;
    @include media-breakpoint-up(md) {
      > li {
        -ms-flex: 0 0 auto;
        flex: 0 0 auto;
        width: auto;
        max-width: none; } } }
  .ie9 & {
    .nav {
      display: table;
      > li {
        display: table-cell; } } } }
.navbar-collapse.collapse:not(.show) {
  @include media-breakpoint-up(sm) {
    display: block; }
  @include media-breakpoint-down(md) {
    display: none; } }
.navbar-toggle {
  //background-color: $secondary
  color: #FFF;
  display: block;
  @include size(40px,38px);
  padding: 4px 0;
  @include media-breakpoint-up(lg) {
    display: none; }
  .bar {
    display: block;
    @include size(22px, 0);
    border-top: 2px solid;
    margin: 6px auto;
    position: relative;
    transition: transform .35s ease, opacity .35s ease; }
  &:not(.collapsed) {
    .bar:first-child {
      transform: translate(0,8px) rotate(45deg); }
    .bar:nth-child(2) {
      opacity: 0; }
    .bar:last-child {
      transform: translate(0,-8px) rotate(-45deg); } } }
.navbar-right .nav {
  @include media-breakpoint-up(md) {
    justify-content: flex-end; } }
.dropdown-box {
  display: none;
  @extend %nolist;
  @include media-breakpoint-up(lg) {
    display: block;
    position: absolute;
    top: 100%;
    right: 50%;
    transform: translate(50%,0);
    z-index: 999;
    min-width: 100%;
    white-space: nowrap;
    background-color: #FFF;
    padding: 5px 0;
    border-top: 1px solid #ddd;
    text-align: center;
    visibility: hidden;
    opacity: 0;
    transition: opacity .35s ease;
    box-shadow: 0 3px 3px 0 rgba(0,0,0,.35);
    li {
      border: 0 !important; }
    a {
      padding: 7px 15px;
      text-decoration: none; } } }
.dropdown {
  &:hover {
    .dropdown-box {
      opacity: 1;
      visibility: visible; } } }
