.pagination {
  // margin: 25px 0
  .pages {
    margin: 0 -4px; }
  li {
    display: inline-block;
    padding: 0 4px;
    vertical-align: middle;
    @include media-breakpoint-down(sm) {
      padding: 0 2px; } }
  a {
    height: 30px;
    line-height: 28px;
    min-width: 30px;
    padding: 0 7px;
    text-align: center;
    transition: all .35s;
    border: 1px solid #cccccc;
    color: #666666;
    display: block;
    &:hover {
      background-color: $primary;
      border-color: $primary;
      color: #FFF; } }
  .active {
    a {
      background-color: $primary;
      border-color: $primary;
      color: #FFF; } } }
.pages-v1 {
  .next {
    a:before {
      content: "下一页"; } }
  .end {
    a:before {
      content: "尾页"; } }
  .prev {
    a:before {
      content: "上一页"; } }
  .first {
    a:before {
      content: "首页"; } } }
.pages-v2 {
  .next,.end,.first,.prev {
    a:before {
      @extend %bbcicon;
      display: inline-block;
      font-family: "iconfont" !important; } }
  .next {
    a:before {
      content: "\e63e"; } }
  .prev {
    a:before {
      content: "\e642"; } }
  .first {
    a:before {
      content: "\e900"; } }
  .end {
    a:before {
      content: "\e901"; } } }
