@mixin flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }
@mixin flex-wrap {
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }
@mixin flex-width($width) {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 $width;
  flex: 0 0 $width;
  max-width: $width; }
%nolist {
  margin: 0;
  padding: 0;
  list-style: none; }
