.foot-tool {
  display: none; }
@include media-breakpoint-down(sm) {
  .foot-tool {
    @for $i from 1 through 4 {
      &[data-foot='#{$i}'] {
        li {
          width: percentage(1/$i); } } }
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    flex-wrap: wrap;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0,0,0,.6);
    // background-color: $primary
    // border-top: 2px solid #fff
    z-index: 998;
    li {
      // width: 25%
      // flex: 0 0 25%
      // &:not(:last-child)
      //   border-right: 2px solid #fff
      // &:nth-child(4n)
      //   border-right: 0
      a {
        display: block;
        text-align: center;
        padding: 15px 10px;
        color: #ffffff;
        text-decoration: none;
        font-size: 16px;
        line-height: 1.5;
        transition: all .35s;
        i {
          margin-right: 5px;
          font-size: 18px;
          vertical-align: text-bottom;
          line-height: 1;
          // display: block
 }          // margin-bottom: 5px
        &:hover {
          background-color: darken($primary,5%); } } }
    .popup_more {
      position: relative;
      &:before {
        position: absolute;
        @include size(100%);
        top: 0;
        left: 0;
        content: '';
        display: block; } } }
  .copyright {
    padding-bottom: 60px; } }
