

select {
  &.bs-select-hidden, &.selectpicker {
    display: none !important; } }
$bootstrap-select-indicator: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'%3E%3Cpath fill='#888' d='M151.5 347.8L3.5 201c-4.7-4.7-4.7-12.3 0-17l19.8-19.8c4.7-4.7 12.3-4.7 17 0L160 282.7l119.7-118.5c4.7-4.7 12.3-4.7 17 0l19.8 19.8c4.7 4.7 4.7 12.3 0 17l-148 146.8c-4.7 4.7-12.3 4.7-17 0z'/%3E%3C/svg%3E");
.btn-js-select,.js-select.form-control:not(.btn-group) {
  background-color: inherit;
  color: inherit;
  border: 1px solid #ccc;
  width: 100%;
  text-align: left;
  background-image: $bootstrap-select-indicator !important;
  background-repeat: no-repeat;
  background-position: right .5rem center;
  background-size: 1rem 1rem;
  padding-right: 1.75rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  &:focus {
    box-shadow: none;
    outline: 0; } }

.js-select {
  position: relative;
  > {
    .dropdown-toggle {
      width: 100%;
      padding-right: 25px;
      z-index: 1;
      &.bs-placeholder {
        color: #999;
        &:hover, &:focus, &:active {
          color: #999; } } }
    select {
      display: none; } } }
.js-select {
  @include media-breakpoint-down(md);
  &.mobile-false {
    > select {
      position: absolute !important;
      top: 0;
      left: 0;
      opacity: 0;
      display: block !important;
      @include size(100%);
      z-index: 2; } } }

.has-error .js-select .dropdown-toggle, .error .js-select .dropdown-toggle {
  border-color: $danger; }

.js-select {
  &.fit-width {
    width: auto !important; }
  &:not([class*="col-"]):not([class*="form-control"]):not(.input-group-btn) {
    width: 220px; }
  &.form-control.btn-group {
    margin-bottom: 0;
    padding: 0;
    border: 0;
    &:not([class*="col-"]) {
      width: 100%; }
    &.input-group-btn {
      z-index: auto;
      &:not(:first-child):not(:last-child) > .btn {
        border-radius: 0; } } }
  &.btn-group {
    &:not(.input-group-btn), &[class*="col-"] {
      float: none;
      display: inline-block;
      margin-left: 0; }
    &.dropdown-menu-right, &[class*="col-"].dropdown-menu-right {
      float: right; } } }

.row .js-select.btn-group[class*="col-"].dropdown-menu-right {
  float: right; }

.form-inline .js-select.btn-group, .form-horizontal .js-select.btn-group, .form-group .js-select.btn-group {
  margin-bottom: 0; }

.form-group-lg .js-select.btn-group.form-control, .form-group-sm .js-select.btn-group.form-control {
  padding: 0; }

.form-group-lg .js-select.btn-group.form-control .dropdown-toggle, .form-group-sm .js-select.btn-group.form-control .dropdown-toggle {
  height: 100%;
  font-size: inherit;
  line-height: inherit;
  border-radius: inherit; }

.form-inline .js-select.btn-group .form-control {
  width: 100%; }

.js-select {
  .dropdown-toggle {
    &:after {
      display: none; } }
  &.btn-group {
    &.disabled {
      cursor: not-allowed;
      &:focus {
        outline: none !important; } }
    > .disabled {
      cursor: not-allowed; }
    > .disabled:focus {
      outline: none !important; }
    &.bs-container {
      position: absolute;
      height: 0 !important;
      padding: 0 !important;
      .dropdown-menu {
        z-index: 1060; } }
    .dropdown-toggle {
      &:after {}
      .filter-option {
        display: block;
        overflow: hidden;
        width: 100%;
        text-align: left;
        text-overflow: ellipsis; } }
    &[class*="col-"] .dropdown-toggle {
      width: 100%; }
    .dropdown-menu {
      min-width: 100%;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      &.inner {
        position: static;
        float: none;
        border: 0;
        padding: 0;
        margin: 0;
        border-radius: 0;
        -webkit-box-shadow: none;
        box-shadow: none; }
      li {
        position: relative;
        &.active small {
          color: #fff; }
        &.disabled a {
          cursor: not-allowed; }
        a {
          cursor: pointer;
          -webkit-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
          &.opt {
            position: relative;
            padding-left: 2.25em; }
          span {
            &.check-mark {
              display: none; }
            &.text {
              display: inline-block; } } }
        small {
          padding-left: 0.5em; } }
      .notify {
        position: absolute;
        bottom: 5px;
        width: 96%;
        margin: 0 2%;
        min-height: 26px;
        padding: 3px 5px;
        background: #f5f5f5;
        border: 1px solid #e3e3e3;
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
        pointer-events: none;
        opacity: 0.9;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box; } }
    .no-results {
      padding: 3px;
      background: #f5f5f5;
      margin: 0 5px;
      white-space: nowrap; }
    &.fit-width .dropdown-toggle {
      .filter-option {
        position: static; } }
    &.show-tick .dropdown-menu li {
      &.selected a span.check-mark {
        position: absolute;
        display: inline-block;
        right: 15px;
        margin-top: 5px; }
      a span.text {
        margin-right: 34px; } } }
  &.show-menu-arrow {
    &.open > .dropdown-toggle {
      z-index: 1061; } } }
.bs-searchbox, .bs-actionsbox, .bs-donebutton {
  padding: 4px 8px; }

.bs-actionsbox {
  width: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  .btn-group button {
    width: 50%; } }

.bs-donebutton {
  float: left;
  width: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  .btn-group button {
    width: 100%; } }

.bs-searchbox {
  + .bs-actionsbox {
    padding: 0 8px 4px; } }
.dropdown-menu.inner {
  display: block;
  a {
    display: block;
    padding: 0.25em 0.75em;
    color: #444;
    outline: 0;
    &:hover {
      background-color: #f5f5f5; } }
  .selected {
    a {
      background-color: #f5f5f5;
      color: $primary; } } }
.js-select-searching {
  li {
    a {
      display: none !important; }
    &.no-results {
      a {
        display: block; } } } }
.js-select-loader {
  position: relative;
  .spinner {
    position: absolute;
    bottom: 0.375rem;
    right: 0.5rem;
    background-color: #ffffff;
    z-index: 99; } }
