.header-v2 {

  a {
    text-decoration: none !important; }
  .navbar {
    background-color: $primary;
    @include media-breakpoint-down(sm) {
      .container {
        padding: 0; } } }
  .header-brand {
    padding: 20px 0;
    border-bottom: 1px solid #eee;
    @include media-breakpoint-down(sm) {
      padding: 12px 0;
      .container {
        max-width: 100%; } } }
  .header-hotline {
    font-size: 20px;
    @include media-breakpoint-down(sm) {
      font-size: 24px;
      span {
        display: none; } } }
  .hotline-icon {
    font-size: .875em;
    @include size(2em);
    border: 1px solid;
    line-height: 2em;
    text-align: center;
    border-radius: 50%;
    margin-right: 8px;
    @include media-breakpoint-down(sm) {
      margin-right: 0; } }
  .navbar-container {
    position: relative;
    @include clearfix; }
  .nav-toggle-wrap {
    width: 0;
    float: right;
    overflow: hidden; }
  .navbar-toggle {
    @include size(40px);
    padding: 5px 0; }
  .navbar-has-hidden {
    .nav {
      justify-content: space-between; }
    .nav-toggle-wrap {
      width: 40px; }
    .navbar-toggle {
      display: block !important;
      color: #FFF; } }
  #more-nav {
    text-align: center;
    background-color: #eee;
    li {
      display: inline-block; }
    a {
      padding: 8px 12px;
      color: #888;
      &:hover {
        color: $primary; } }
    li.active > a {
      color: $primary; } } }
.nav-header-v2 {
  overflow: hidden;
  > li {
    > a {
      color: #FFF;
      padding: 10px 15px;
      white-space: nowrap;
      @include media-breakpoint-down(sm) {
        padding: 10px 13px; }
      &:hover {
        color: #ffffff;
        background-color: darken($primary,10%); } }
    &.active {
      > a {
        color: #ffffff;
        background-color: darken($primary,10%); } } } }

