/*------------------------------------
 *  Box-shadows Styles
 *------------------------------------ */

[class*="shadow--hover"] {
  transition: box-shadow .35s ease; }
.shadow-none {
  box-shadow: none !important;

  &--focus:focus {
    box-shadow: none !important; }

  &--parent-hover {
    .g-parent:hover & {
      box-shadow: none !important; } } }
