@include media-breakpoint-down(sm) {
  .header-holder-v1 {
    height: 67px; } }
.header-v1 {
  background-color: #FFF;
  @include clearfix;
  @include media-breakpoint-down(sm) {
    .header-holder-v1 & {
      position: fixed;
      width: 100%;
      top: 0;
      left: 0; } }
  a {
    text-decoration: none !important; }
  @include media-breakpoint-up(md) {
    .navbar-toggle {
      display: none; } }
  .navbar-toggle {
    color: $primary;
    @include size(42px);
    border-radius: 50%;
    border: 1px solid;
    padding: 6px 0;
    margin-left: 8px; }
  .navbar {
    background-color: $primary; }
  .header-brand {
    padding: 20px 0;
    border-bottom: 1px solid #eee;
    @include media-breakpoint-down(sm) {
      padding: 12px 0;
      .container {
        max-width: 100%; } } }
  .header-hotline {
    font-size: 20px;
    @include media-breakpoint-down(sm) {
      font-size: 24px;
      span {
        display: none; } } }
  .hotline-icon {
    font-size: .875em;
    @include size(2em);
    border: 1px solid;
    line-height: 2em;
    text-align: center;
    border-radius: 50%;
    margin-right: 8px;
    @include media-breakpoint-down(sm) {
      margin-right: 0; } }
  .navbar-main.collapse {
    @include media-breakpoint-up(md) {
      display: block; } }
  @include media-breakpoint-down(sm) {
    .header-logo {
      img {
        height: 42px;
        width: auto; } }
    .header-hotline,.navbar-toggle {
      display: inline-block;
      vertical-align: middle; } } }
@include media-breakpoint-down(sm) {
  .navbar-grid {
    &[data-nav] {
      .container {
        width: 100%;
        max-width: 100%;
        padding: 0; }
      .nav {
        @include flex-wrap;
        border-top: 1px solid darken($primary,10%);
        border-right: 1px solid darken($primary,10%);
        font-size: 14px;
        li {
          @include flex-width(25%);
          border {}
          border: 1px solid darken($primary,10%);
          border-top: 0;
          border-right: 0; } }
      .nav-icon {
        font-size: 24px;
        display: block;
        margin-bottom: 8px; }
      .nav-title {
        opacity: 0.75; } }
    &[data-nav="11"] {

      .nav {
        li:nth-child(8)~li {
          @include flex-width(33.33%); } } }
    &[data-nav="7"] {

      .nav {
        li:nth-child(4)~li {
          @include flex-width(33.33%); } } }
    &[data-nav="10"] {

      .nav {
        li:nth-child(8)~li {
          @include flex-width(50%); } } }
    &.nav10a[data-nav="10"] {

      .nav {
        li:nth-child(4)~li {
          @include flex-width(33.33%); } } }
    &.nav10b[data-nav="10"] {

      .nav {
        li {
          @include flex-width(20% !important);
          .nav-title {
            display: inline-block;
            width: 2em; } } } }
    &[data-nav="9"],&[data-nav="6"],&.nav12a[data-nav="12"] {
      .nav {
        li {
          @include flex-width(33.333%); } } } } }
.nav-header-v1 {
  > li:not(:first-child) {
    @include media-breakpoint-up(md) {
      padding-left: 1px;
      position: relative;
      &:before {
        content: "";
        height: 40%;
        top: 30%;
        left: 0;
        position: absolute;
        border-left: 1px solid darken($primary,10%); } } }
  > li {
    > a {
      padding: 12px 15px;
      text-align: center;
      color: #FFF;
      text-decoration: none;
      margin: 0 -1px;
      transition: background-color .35s ease;
      @include media-breakpoint-down(xs) {
        padding: 12px 12px;
        font-size: 13px; } }
    &:hover,&.active {
      > a {
        background-color: darken($primary,10%); } } } }
