.ajax-load-container {
  text-align: center;
  display: none;
  .ajax-loading &,.ajax-complete & {
    display: block; } }
// .ajax-pagination
//   width: 100%
.ajax-load-btn-txt {
  .ajax-loading &,.ajax-complete & {
    display: none; } }
.ajax-spinner {
  border: 3px solid darken( $primary , 7% );
  border-top: 3px solid #FFF;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
  @include size(21px);
  margin-right: 5px;
  animation: loader-spin 1.2s linear infinite; }
@keyframes loader-spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.masonry-item {
  img {
    transition: all .35s ease !important;
    opacity: 1; } }
.masonry-lazy-error {
  height: 120px;
  position: relative;
  background-color: #ddd;
  &:before {
    @include size(30px);
    content: "×";
    display: block;
    border: 2px solid;
    position: absolute;
    margin: auto;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: $danger;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    border-radius: 50%;
    opacity: .5; }
  img {
    opacity: 0; } }
.masonry-lazy-loading {
  position: relative;
  height: 120px;
  overflow: hidden;
  &:before,&:after {
    @include size(30px);
    content: "";
    display: block;
    border: 3px solid transparent;
    position: absolute;
    margin: auto;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-top-color: $primary;
    border-radius: 50%;
    animation: spinner 1s infinite ease; }
  &:after {
    border-left-color: $primary;
    animation: spinner2 1s infinite linear;
    transform: rotate(20deg); }
  img {
    opacity: 0; } }
@keyframes spinner {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(360deg); } }
@keyframes spinner2 {
  from {
    transform: rotate(20deg); }
  to {
    transform: rotate(380deg); } }
