.lightbox-modal {
  .modal-dialog,.modal-content {
    display: flex;
    width: 100%;
    height: 100%;
    max-width: 100%;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    margin: 0; }
  .modal-content {
    background-color: rgba(0,0,0,.75); } }
.lightbox-control {
  position: absolute;
  top: 50%;
  height: 0;
  width: 100%;
  left: 0;
  z-index: 999;
  > button {
    position: relative;
    z-index: 99; } }
.lightbox-next {
  float: right; }
.lightbox-prev {
  float: left; }
.lightbox-close {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 99; }
.lightbox-container {
  @include size(300px);
  position: relative;
  transition: width .35s, height .35s; }
.lightbox-item {
  @include size(300px);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%,-50%,0);
  margin: auto;
  opacity: 0;
  transition: all .5s ease;
  &:not(.active) {
    @include size(100px !important); }
  &.active {
    opacity: 1;
    z-index: 200; }
  > img {
    @include size(100%);
    position: absolute;
    top: 0;
    left: 0; }
  &.prev-1,&.next-1 {
    @include size(250px !important,300px !important);
    opacity: .85;
    z-index: 80; }
  &.prev-2,&.next-2 {
    @include size(200px !important,250px !important);
    opacity: .85;
    z-index: 40; } }
.lightbox-count {
  position: absolute;
  top: 90%;
  font-size: 22px;
  color: #FFF;
  width: 100%;
  z-index: 99;
  text-align: center; }
